* {
  font-family: 'IBM Plex Sans', sans-serif;
}

* {
  font-family: IBM Plex Sans, sans-serif;
}

.listItem {
  border: 2px solid #001e1d;
  background: #e16162;
  font-weight: bolder;
  opacity: 0.6;
  transition: 0.3s;
  border-radius: 15px;
  padding: 10px;
  margin-top: 10px;
  margin-bottom: 10px;
}
.listItem:hover {
  cursor: pointer;
  opacity: 1;
}

